<template>
    <div class="updateUsers">
      <el-form  :model="formData" label-width="80px">
        <el-form-item label="用户名">
          <el-input v-model="formData.userName"></el-input>
        </el-form-item>
        <el-form-item label="身份证">
          <el-input v-model="formData.idCard"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="formData.pwd"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="formData.phone"></el-input>
        </el-form-item>
        <el-form-item label="性别">
          <el-radio v-model="formData.sex" label="1">男</el-radio>
          <el-radio v-model="formData.sex" label="0">女</el-radio>
        </el-form-item>
        <el-form-item label="入党时间">
          <el-date-picker
            v-model="formData.joinTime"
            type="date"
            placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="政治面貌">
          <el-input v-model="formData.status"></el-input>
        </el-form-item>
        <el-form-item label="图片">
          <el-upload
            class="avatar-uploader"
            action="https://upload-z1.qiniup.com"
            :show-file-list="false"
            :on-success="upload"
            :data="{token:token}"
          >
            <img v-if="formData.avatar" :src="formData.avatar" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" round @click="submit">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>
<script>
    export default{
      name: "update",
      data(){
        return{
          formData:{
            userName:'',
            idCard:'',
            pwd:'',
            avatar:'',
            phone:'',
            sex:'',
            joinTime:'',
            status:''
          },
          token:''
        }
      },
      methods:{
        //数据回填
        getData() {
          this.$axios.get('getUsers',{id: this.$route.query.id}).then(res => {
            this.formData = res.data
            console.log(res.data)
          })
        },
        getToken(){
          this.$axios.qiniuGet().then(res => {
            this.token = res.data
          })
        },
        upload(res){
          this.formData.avatar = res.url
        },
          //修改用户
        submit(){
          let params = {
            ...this.formData,
            id:this.$route.query.id
          }
          this.$axios.post('updateUsers',params).then( res => {
            if(res.code == 200){
              this.$message({
                message:`${res.msg}`,
                type:'success'
              })
              setTimeout( () => {
                this.$router.push('/users')
              },1500)
            }else{
              this.$message({
                message:`${res.msg}`,
                type:'warning'
              })
            }
          })
        }
      },
      created(){
        this.getToken();
        this.getData()
      }
    }
</script>
<style scope>
  .updateUsers{
    height:100%;
    padding-top: 20px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



